/* eslint-disable react/button-has-type */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import ExpandMoreTwoToneIcon from '@material-ui/icons/ExpandMoreTwoTone';
import { store } from '../../../store';
import agent from '../../../agent';
import {
  GET_LINKS, GET_USER_DETAILS, GET_PROFILE_VIEWS, GET_BG_STYLE,
} from '../../../constants/actionTypes';

import CollapseTransition from '../../../common/Components/CollapseTransition';

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      weekly:0,
      total:0,
      monthly:0,
      showProAnalytics: false,
    };
    this.profileViews=[]
    this.togelShowProAnalytics = this.togelShowProAnalytics.bind(this);
  }

  componentWillMount() {
    // console.log(this.props);
    this.props.getProfileViews();
  }

  componentWillReceiveProps(np) {
    console.log('Profile views', np.profileViews);
    if(np.profileViews){
        let onWeekAgo = new Date();
        onWeekAgo.setDate(onWeekAgo.getDate() - 7)
        let onMonthAgo = new Date();
        onMonthAgo.setDate(onMonthAgo.getDate() - 7)
        let total = 0;
        let monthly = 0;
        let weekly = 0;
        this.profileViews = np.profileViews.analytics;
        this.profileViews.forEach((v ) => {
            total += v.views;
            let date = new Date(v.day)
            if(date.getTime() >= onWeekAgo.getTime()){
                console.log(v.views)
                weekly+= v.views;
            }
            if(date.getTime() >= onMonthAgo.getTime()){
                monthly  += v.views;
            }
        })
        this.setState({total,monthly,weekly})
    }
  }

  resize() {
    this.setState({ mobileView: window.innerWidth <= 600 });
  }

  togelShowProAnalytics() {
    this.setState({ showProAnalytics: !this.state.showProAnalytics });
  }

  render() {
      const { weekly, monthly, total } = this.state
    return (
      <div className='bb ' >
        <div className="  bg-white center flex tc items-center  justify-around  " onClick={() => this.togelShowProAnalytics()}>
          <b className="tl f6">
              Lifetime Analysis
          </b>

          <p>
Views
            <span className="b  blue"> {total} </span>
          </p>
          <p>
Clicks
            <span className="b  blue"> 60 </span>
          </p>
          
          <ExpandMoreTwoToneIcon />
        </div>
        <CollapseTransition visible={this.state.showProAnalytics}>
          <div>
            <div className="  profile-views  center flex tc justify-around ">
              <b className="tl">
                Last 28 Days
              </b>
              <div className="flex justify-around  w-80 center tl ">
                <p>
                    Views
                  <span> {monthly} </span>
                </p>
                <p>
                    Clicks
                  <span> 60 </span>
                </p>
                
              </div>
            </div>
            <div className="  profile-views  center flex tc justify-around ">
              <b className="tl">
                Last 7 Days
              </b>
              <div className="flex justify-around  w-80 center tl ">
                <p>
                    Views
                  <span> {weekly} </span>
                </p>
                <p>
                    Clicks
                  <span> 60 </span>
                </p>
                
              </div>
            </div>
          </div>
        </CollapseTransition>

      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  links: state.AdminPageReducers.links,
  profileViews: state.AdminPageReducers.profileViews,

});

const mapDispatchToProps = (dispatch) => ({
  getUserDetails: (user) => store.dispatch({ type: GET_USER_DETAILS, payload: agent.Auth.getUserDetails(user) }),
  getLinks: () => dispatch({ type: GET_LINKS, payload: agent.AdminPage.getLinks() }),
  getBackgroundStyle: (user) => dispatch({ type: GET_BG_STYLE, payload: agent.AdminPage.getBackground(user) }),
  getProfileViews: () => dispatch({ type: GET_PROFILE_VIEWS, payload: agent.AdminPage.getProfileViews() }),

});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
